            <b-col class="text-left margin4">

<b>1. Use Time to Your Advantage</b>
<p>
    Fill in the answer sheet as you go. In the exam room, youre going to receive two things: (1) a booklet with all the questions and (2) an answer sheet. The answer sheet is the most important document because all your answers must be recorded in it. If you only write your answers in the booklet, your answers wont be taken into account and scored.
    Many test-takers waste a lot of time by writing the answers in the booklet first and then transferring them to the answer sheet. And quite a lot of test-takers find out that they dont have enough time at the end to transfer all of their answers onto the answer sheet. Imagine what a waste it would be to have all the answers but no time to write them down where it really matters!

</p>
<p>
    So, what we advise you to do is to fill in the answer sheet as you go, not at the end. Have the booklet in front of you so you can read the texts and questions comfortably, and also keep the answer sheet somewhere handy so you can write all the answers down as you progress with the test.

</p>    
<p>
    Leave difficult questions for the end. If you spend a lot of time on questions that you find difficult, youll be wasting valuable exam time. You wont be able to answer all the easier questions and youll lose points! If you dont know the right answers to some questions, leave them and move on. This way, you can focus on all the questions you do know the answers to. You can return to the difficult questions at the end if you have the time.
    Dont start reading the text before looking at the tasks. When you start reading, you should do so with some questions in mind. Otherwise, youll read the text, then the questions, then the text again. Youll end up reading the texts too many times, and you simply dont have time for that. Read the tasks and questions first, before you read, and think about the information you need to find to answer those questions while youre reading.
    Practice doing lots of reading tests. This is the best way to use your time before the exam! Youll learn how to take the test before taking the real test. Youll know where your strengths and weaknesses are. Youll also know what to do for each task, and how to answer each type of question. This way youll know how to best divide your time and wont stress too much about working against the clock.

</p>   
<b>2. Read the Task Carefully</b>
<p>
    Whenever you start doing a task, make sure you read the instructions and the examples carefully.

</p>    
<p>
    Most tasks look the same, so youll recognize them at first sight after doing some practice tests. However, there are some details you need to pay special attention to. Here is what you should pay attention to in three of the most common task types:

</p>    
<p>
    True/false/not given: If you select true, then the whole sentence must be true. There are a few tricky questions in which not all of the details are true. Some may be true while others are false. In these cases, the answer will be false.
    Matching tasks: Dont cross out the options youve already used. This may seem like a fast way of doing the task but it can lead to mistakes. Instead, reconsider all the options for each question. This way youll have the opportunity to correct your own mistakes by seeing if each word option is a better fit for another question.
    Gap filling tasks: Make sure you dont go over the word limit for each gap.
</p>
<b>3. Get Better at Scanning</b>
<p>
    Scanning is a reading method that allows you to find information faster. When scanning, you no longer read everything word for word. You just move your eyes across the text smoothly in a wavelike motion. You dont stop to read details and you dont waste time with unnecessary information. This is a great method for understanding the main ideas of a text and for finding the information youll want to read in more detail.

</p>    
<p>
    When you really want to understand a text, youll want to understand what each paragraph says. Each paragraph has a main idea and that idea is expressed in the topic sentence. You dont have time to read all the details and thats okay because most of them arent needed to answer the questions correctly. What you really need to understand each paragraph is the information in the topic sentence.

</p>    
<p>
    So, where do you find the topic sentence?

</p>    
<p>
    Usually, its the first sentence of a paragraph, but it can also appear at the end. Its the sentence that expresses the main idea of the whole paragraph. Topic sentences are easy to recognize because they sound like theyre announcing whats coming next. Something like There are many advantages to using intranets in companies nowadays is the topic sentence of a paragraph thats going to discuss advantages of intranets. To maximize your time, look for it in the first sentence, then the second one and then the last one. Do this while youre reading.

</p>    
<p>
    Scanning can also help you find key words and numbers fast. While numbers are usually easy to locate, with key words you have to use your memory to find the approximate location where you read that earlier and then look for the word being discussed in more detail. You should also be looking for key words and numbers in figures, diagrams and footnotes.

</p>    
<b>4. Be Cool with Vocabulary</b>
<p>
    Dont panic if you come across unknown words.

</p>    
<p>
    Even native speakers dont understand every single word in every text they read and thats okay because all those words dont matter most of the time.

</p>    
<p>
    Remember that this isnt a vocabulary test!

</p>    
<p>
    You arent allowed to use dictionaries in the exam but this shouldnt be a problem at all. Youd be wasting a lot of time in the exam by looking up every word you dont know. So, you should forget about using them while practicing reading at home as well. When youre practicing reading anything or taking a FCE practice test, do not use a dictionary. Make yourself complete all your reading practice first. Then, once you have completed the activity, you can return to the text and look something up afterward.

</p>    
<p>
    You should be training yourself to read faster and understand overall ideas in the texts. You can understand the meaning of a sentence or paragraph even if you dont know every single word!

</p>    
<p>
    If you want to improve your vocabulary, you can work on that separately after completing the FCE reading practice test youre working on. There are texts with some really important words which are considered specific knowledge from a certain field and which are given in a glossary at the end of the text. Whenever you see a glossary, you should read it to improve your understanding of such texts.

</p>    
<p>
    You may also come across questions which used rephrased ideas or synonyms of words that appear in the texts. For example, you may have a paragraph about the disadvantages of exposing kids to too much television. The question may not use the word disadvantages like the text does, but it might use a synonym such as downsides or drawbacks instead. So youre definitely better off if your vocabulary is rich, but you still have to be cool about finding unknown words.

</p>    
<p>
    Learn as much vocabulary as you can, but dont stress if you dont know every single word in a text.

</p>    
<b>5. Improve Your Reading Speed</b>
<p>
    Theres only one way to do this and theres no hiding from it: the more you read, the better and faster you get at reading.

</p>    
<p>
    You should be doing practice tests to work on reading texts and questions. Even in your spare time when youre relaxing and reading fun texts, like novels and comic books, try reading a bit faster every time. You may even want to time yourself to check if youre making progress with your reading speed.
</p>  
